body::-webkit-scrollbar {
  display: none;
}

.home-img {
    width: 100%;
    height: auto;
    display: block;
}

.home-tile {
    width: 50vw;
    height: 50vw;
    float: left;
    max-width: 100%;
}

#flavor-wrapper {
    font-size: 1.5em;
    color: white;
    background-color: #343534;
    font-weight: 100;
    line-height: 1.7;
}

#home-flavor-text {
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

#refresh-button {
    background-color: #343534;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.back2 {
    background-color: #61c5c1 !important;
}

#refresh {
    width: 50%;
}

.remove {
    -webkit-filter: grayscale(100%) contrast(175%) brightness(70%);
            filter: grayscale(100%) contrast(175%) brightness(70%);
}

@media screen and (min-width: 70em) {
    .home-tile {
        height: calc((100vh - 3rem)/2);
        width: calc((100vh - 3rem)/2);
        float: left;
    }

    #flavor-wrapper {
        display: inline-block;
        float: left;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        font-size: 2em;
        height: calc(100vh - 3rem);
        width: calc(100vw - (100vh - 3rem)*3/2);
        padding: 0 1vw;
        background-color: #61c5c1;
    }

    #home-flavor-text {
        -ms-flex-pack: left;
            justify-content: left;
    }
}



